


























































































































.container {
  background-color: #ebebeb;
  color: #333;
  font-size: 28px;

  .search-city {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-color: #f5f5f5;
    padding: 22px 30px;
    box-sizing: border-box;
  }

  .search-input {
    height: 2em;
    background: #fff;
    padding-left: 60px;
    color: #999;
  }
}

.icon-sousuo {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
  color: #999;
}

/* 搜索结果区样式 */

.result-box {
  min-height: calc(100vh - 100px);
  margin-top: 100px;
  background: #f5f5f5;
}

.result-item {
  height: 88px;
  line-height: 88px;
  margin-left: 30px;
  border-bottom: 1px solid #c8c7cc;
}

/* 城市列表样式 */
.section-title {
  padding-left: 30px;
  line-height: 60px;
}

.section-body {
  background-color: #f5f5f5;
  padding-right: 60px;

  .section-item:last-child {
    border: none;
  }

  .section-item {
    height: 88px;
    line-height: 88px;
    margin-left: 30px;
    border-bottom: 1px solid #c8c7cc;
  }
}

.section-body-inline {
  display: flex;
  flex-wrap: wrap;
  background-color: #f5f5f5;
  padding-bottom: 30px;
  padding-left: 30px;

  .section-item {
    min-width: 26%;
    background: #fff;
    margin-top: 30px;
    margin-right: 4%;
    border-radius: 6px;
    line-height: 66px;
    text-align: center;
    box-sizing: border-box;
    border: 1px solid #e6e6e6;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 30px;
  }
}

/* 城市侧边栏导航样式 */

.citylist-nav {
  position: fixed;
  top: 13vh;
  right: 0;
  width: 30px;
  text-align: center;

  > view {
    font-size: 24px;
    line-height: 1.5;
  }
}
